.app__flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  width: max-content;
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem 1.2rem;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  border-radius: .4rem;

  &:hover{
    display: inline-block;
    box-shadow: var(--shadow);
  }
}
